import React from "react";
import YouTube from "react-youtube";
import styles from "./style.module.css";

const YouTubePlayer = ({ options, ...props }) => {
    const opts = {
        ...options,
        frameborder: 0,
        playerVars: {
            modestbranding: 1,
            rel: 0,
        },
    };

    return <YouTube className={styles.container} opts={opts} {...props} />;
};

export default YouTubePlayer;
